@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  box-sizing: border-box;
}

div.principalContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
}

div.header {
  display: flex;
  align-items: center;
  height: 125px;
  width: 98vw;
  background-color: #43bea8;
}

p.title {
  font-size: 30px;
  color: #ffffff;
  margin-left: 40px;
  margin-bottom: 5px;
}

p.content {
  font-size: 15px;
  color: #ffffff;
  margin-left: 40px;
}

div.filters {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 45px;
  width: 98vw;
  background-color: #4496d0;
}

input {
  width: 200px;
  height: 30px;
  margin-right: 20px;
  border: none;
  border-radius: 5px;
}

select {
  width: 150px;
  height: 30px;
  margin-right: 20px;
  border: none;
  border-radius: 5px;
}

button.clean {
  width: 150px;
  height: 30px;
  border: none;
  border-radius: 5px;
  color: #ffffff;
  background: rgba(255, 0, 0, 0.8);
}

div.contenedorResults {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  width: 98vw;
  margin-top: 80px;
}

img.error404 {
  width: 60%;
}

div.cardContainer {
  display: flex;
  flex-direction: column;
  width: 300px;
  height: 621px;
  border: 1px #dfdfdf solid;
  margin-right: 10px;
  margin-bottom: 20px;
}

div.cardContent {
  margin: 20px 20px 0 20px;
}

img.image {
  width: 300px;
}

h1.title {
  color: #393939;
  font-size: 20px;
  margin-bottom: 20px;
}

p.description {
  color: #4b4b4b;
  font-size: 14px;
  line-height: 20px;
}

div.hotelDetails {
  display: flex;
  margin-bottom: 20px;
}

div.bedContainer,
div.pinContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px 0 0 5px;
  background: #5c9beb;
  height: 25px;
  width: 25px;
}

img.bed,
img.pin {
  height: 15px;
}

div.price,
div.size,
div.country {
  display: flex;
  align-items: center;
  background: #f5f5f5;
  height: 25px;
  border-radius: 5px;
}

div.size {
  margin-right: 10px;
}

div.country {
  margin: 20px 0 15px 0;
}

div.price {
  background: #5c9beb !important;
  color: #7ab3ef;
  padding: 5px;
}

p.textCountry,
p.textSize {
  padding: 10px;
}

button.button {
  width: 300px;
  height: 45px;
  color: #ffffff;
  font-size: 16px;
  background: #43bea8;
  border: none;
}

div.buttonContainer {
  display: flex;
  height: 100%;
  align-items: flex-end;
}

